import React, { Component } from "react";
import { Button } from ".";


class Topbar extends Component {
   state = {
      width: window.innerWidth,
      SideActive: false,
   }

   componentDidMount() {
      window.addEventListener('resize', () => {
         this.setState({
            width: window.innerWidth
         })
      });
   }

   render() {
      return (
         <nav>
            <div className='lua-topbar'>
               {this.state.width <= 600 && (
                  <div style={{padding: '3px'}}>
                     <Button onClick={() => {
                        this.setState({
                           SideActive: !this.state.SideActive
                        });

                        const Sidebar = document.getElementById('lua-sidebar');

                        if (Sidebar) {
                           if (this.state.SideActive) {
                              Sidebar.style.width = `${window.innerWidth - 50}px`;
                           } else {
                              Sidebar.style.width = '0px';
                           }
                        }
                     }}>
                        =
                     </Button>
                  </div>
               )}
               <h2>Lua.Org Remaster</h2>
            </div>
         </nav>
      )
   }
}


export default Topbar;