import React from "react";
import { createRipples } from 'react-ripples';

const Button = (
   {children, onClick, duration, color}: {
      children?: any,
      onClick?: Function,
      duration?: number,
      color?: string,
   }
) => {
   const Ripple = createRipples({
      color: color || 'rgba(255, 255, 255, 0.5)',
      during: duration || 300,
      className: 'lua-ripple',
   });

   return (
      <Ripple>
         <button 
            className='lua-button'
            onClick={() => {
               onClick && onClick();
            }}
         >
         {children}
         </button>
      </Ripple>
   )
}

export default Button;