import React from 'react';
import { Route, BrowserRouter } from 'react-router-dom';
import { Sidebar, Topbar } from './component';
import { Tree } from './interface';
import { Home } from './routes';

const RemappedTree: Array<{name: string, component: any, path: string}> = [];

Object.keys(Tree).forEach(key => {
  const TreeData = Tree[key];

  TreeData.forEach(data => {
    RemappedTree.push(data);
  });
});

function App() {
  return (
    <BrowserRouter>
      <Sidebar />
      <div className='lua-wrapper'>
        <Topbar />
        <Route component={Home} path='/' exact />
        {RemappedTree.map((data, index) => {
          return <Route key={`route-element-${index}`} component={data.component} path={data.path}  />
        })}
      </div>
    </BrowserRouter>
  );
}

export default App;
