import { Button } from '.';
import { RouteComponentProps, withRouter } from 'react-router-dom';

interface LinkProps extends RouteComponentProps {
   path: string, children?: any
}

function _Link({path, children, history}: LinkProps) {
   return <Button onClick={() => {
      history.replace(path);
   }}>
      {children}
   </Button>
}

const LinkButton = withRouter(_Link);

export { LinkButton };