import React, { Component } from "react";
import { Container, Divider } from "../component";


function CreateThread({children, title}: {
   children: any,
   title: string
}) {
   
   return function() {
      return (
         <Container>
            <h2>
               {title}
            </h2>
            <Divider margin Direction='horizontal' />
            {children}
         </Container>
      )
   }
}

export default CreateThread;