interface TypicalInterface {
   text: string,
   duration?: number
}

/**
 * 
 * @param config 
 * @param onUpdate 
 * 
 * duration will be counted as ms
 */

function Typical(config: TypicalInterface, onUpdate: Function) {
   var { text, duration } = config;

   if (!duration) duration = 1000;
   if (typeof(text) !== 'string') return;

   const length = text.length;
   const timeForPerText = Math.floor(duration / length);

   for (let i = 0; i<length; i++) {
      setTimeout(() => {
         onUpdate(text.substring(0, i + 1));
      }, i * timeForPerText);
   }
   
}

export default Typical;