// Credit: https://github.com/react-syntax-highlighter/react-syntax-highlighter

import React from "react";
import SyntaxHighlighter from 'react-syntax-highlighter';
import { vs2015 } from 'react-syntax-highlighter/dist/esm/styles/hljs';

function Syntax({children}: {children?: any}) {
   return (
      <SyntaxHighlighter language="lua" style={vs2015} showLineNumbers>
         {children || ''}
      </SyntaxHighlighter>
   )
}

export { Syntax };